<template>
  <div>
    <v-card max-width="750" style="margin: 5rem auto 0 auto">
      <v-card-title> Contact Us </v-card-title>
      <v-container>
        <v-form-base :schema="schema" :model="model"></v-form-base>
      </v-container>
      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn text @click="showID">Send</v-btn>
      </v-card-actions>
      <v-text v-if="id != ''"> Example Contact ID: {{ id }} </v-text>
    </v-card>
  </div>
</template>

<script>
  import VFormBase from "vuetify-form-base";
  import { mapActions } from "vuex";
  export default {
    components: {
      VFormBase,
    },
    data: () => ({
      id: "",
      model: {},
      schema: {
        subject: {
          label: "Which service interests you?",
          type: "select",
          items: [
            "Ecuadorian Bullion",
            "Other Bullion",
            "Private Broker Service",
            "I Want To Sell",
            "Other Investment Opportunities",
          ],
          col: 12,
        },
        message: {
          type: "textarea",
          label: "Give us some details about your requirements",
          col: 12,
        },
      },
    }),
    methods: {
      ...mapActions(["getID"]),
      showID() {
        this.getID(this.model.subject).then((id) => (this.id = id));
      },
    },
  };
</script>