<template>
  <v-app>
    <v-app-bar app color="yellow lighten-2" clipped-left>
      <v-app-bar-title> Plata Oro EC </v-app-bar-title>
      <v-spacer v-if="!$vuetify.breakpoint.mdAndUp"></v-spacer>
      <v-app-bar-nav-icon
        v-if="!$vuetify.breakpoint.mdAndUp"
        @click="showNav = !showNav"
      ></v-app-bar-nav-icon>
    </v-app-bar>

    <v-navigation-drawer
      clipped
      app
      :permanent="$vuetify.breakpoint.mdAndUp"
      v-model="showNav"
    >
      <v-simple-table>
        <tbody>
          <tr
            v-for="(tab, index) in tabs"
            :key="`nav-tab-${index}`"
            :class="index == selectedTab ? 'active' : ''"
            style="cursor: pointer"
            @click="goPath(tab.path)"
          >
            <td>
              {{ tab.name }}
            </td>
          </tr>
        </tbody>
      </v-simple-table>
    </v-navigation-drawer>

    <v-main>
      <router-view />
    </v-main>
  </v-app>
</template>

<script>
  import { mapActions } from "vuex";
  export default {
    name: "App",

    data: () => ({
      showNav: false,
    }),

    computed: {
      tabs() {
        return this.$router.options.routes.filter((route) => route.meta.visible);
      },
      selectedTab() {
        const list = this.tabs.map(({ path }) => path);
        return list.indexOf(this.$route.path);
      },
    },

    methods: {
      ...mapActions(["getContents"]),
      goPath(path) {
        if (this.$route.path != path) this.$router.push(path);
        if (this.$route.meta.remote) this.getContents(path);
      },
    },

    mounted() {
      this.getContents(this.$route.path);
    },
  };
</script>